<template>
  <div>
 <van-field v-model="content" 
 :label="label" 
 :type="type" 
 :placeholder='placeholder'
 :rule="rule"
  />

  </div>
</template>

<script>
export default {
props:['label','type','placeholder','rule'],
data(){
    return{
        content:''
    }
},
watch:{
content(){
    this.handle()
}
},
methods:{
    handle(){
const rel= new RegExp(this.rule)
if(rel.test(this.content)){
this.$emit('inputchange',this.content)
}
    }
}
}
</script>

